<template>
  <div>
    <div class="card" style="width: 18rem;">
      <img :src="img" class="card-img-top" alt="..." />
      <div class="card-body">
        <h2 class="card-title">{{this.faceta}}</h2>
        <h5 class="card-title">{{this.covid[0].Date}}</h5>
        <table class="table">
          <thead>
            <tr>
              <th scope="col">Country</th>
              <th scope="col">{{this.faceta}}</th>
            </tr>
          </thead>
          <tbody v-if="this.faceta === 'NewDeaths'">
            <tr v-for="c in filterCountries" :key="c.Country" >
              <td>{{c.Country}}</td>
              <td>{{c.NewDeaths}}</td>
            </tr>
          </tbody>
          <tbody v-if="this.faceta === 'NewConfirmed'">
            <tr v-for="c in filterCountries" :key="c.Country" >
              <td>{{c.Country}}</td>
              <td>{{c.NewConfirmed}}</td>
            </tr>
          </tbody>
          <tbody v-if="this.faceta === 'NewRecovered'">
            <tr v-for="c in filterCountries" :key="c.Country" >
              <td>{{c.Country}}</td>
              <td>{{c.NewRecovered}}</td>
            </tr>
          </tbody>
        </table>
        <a href="#" class="btn btn-primary">Go somewhere</a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "HelloWorld",
  props: {
    faceta: String,
    numbCountries: String,
    img2: String
    
  },
  data() {
    return {
      covid: [],
      countries: [],
      selectedCountry: "Portugal",
    };
  },
  async created() {
    this.getAllInfo();
    if (this.faceta === "NewConfirmed") {
      this.img = require("../assets/confirmed.jpg");
    } else if (this.faceta === "NewDeaths") {
      this.img = require("../assets/cross.jpg");
    } else {
      this.img = require("../assets/success.png");
    }
  },
  methods: {
    async getAllInfo() {
      const response = await fetch("https://api.covid19api.com/summary");
      const myData = await response.json();
      this.covid = myData.Countries;
      if (this.faceta == "NewConfirmed") {
        this.covid.sort(this.compareNewConfirmed);
      }
      if (this.faceta == "NewDeaths") {
        this.covid.sort(this.compareNewDeaths);
      }
      if (this.faceta == "NewRecovered") {
        this.covid.sort(this.compareNewRecovered);
      }
    },
    compareNewConfirmed(a, b) {
      if (a.NewConfirmed > b.NewConfirmed) return -1;
      if (a.NewConfirmed < b.NewConfirmed) return 1;
      else return 0;
    },
    compareNewDeaths(a, b) {
      if (a.NewDeaths > b.NewDeaths) return -1;
      if (a.NewDeaths < b.NewDeaths) return 1;
      else return 0;
    },
    compareNewRecovered(a, b) {
      if (a.NewRecovered > b.NewRecovered) return -1;
      if (a.NewRecovered < b.NewRecovered) return 1;
      else return 0;
    }
  },
  computed: {
    filterCountries() {
      return this.covid.slice(0, this.numbCountries);
    }
  }
};
</script>

